html {
  font-style: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  bottom: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  height: 100%;
  background: url(assets/images/bg-intro-desktop.png) #ff7a7a;
  font-family: "Poppins", sans-serif;
  position: relative;
}

.card {
  border-radius: 0.625rem;
  min-width: 34.75rem;
  width: 100%;
}
.card--info {
  background-color: #6055a5;
  height: 4.0625rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.5625rem;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.2);
}
.card--info .info__text {
  font-weight: 700;
}
.card--info .info__text span {
  font-weight: 400;
}
.card--form {
  background-color: #ffffff;
  padding: 3rem;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.15);
}

form {
  display: flex;
  flex-direction: column;
}
form .form-control {
  height: 3.75rem;
  border-radius: 0.25rem;
}
form input {
  padding-left: 1.6875rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: #3e3c49;
  font-size: 1rem;
  width: 100%;
  border: 0.0625rem solid #b9b6d3;
}
form input::placeholder {
  color: #6e6c7f;
}
form input:focus {
  outline: 0.0625rem solid #3e3c49;
}
form input.error-message {
  border: 3px solid #ff7a7a;
}
form .terms {
  margin-top: 1.25rem;
  text-align: center;
}
form .terms a {
  font-weight: 700;
}

.btn-green {
  background-color: #38cc8c;
  outline: none;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 600;
  position: relative;
  box-shadow: 0px 4px 1px 1px #2dac75;
}
.btn-green:hover {
  background-color: #71eeb8;
  box-shadow: 0px 4px 1px 1px #40ce91;
}
.btn-green:active {
  box-shadow: 0px 2px 1px 1px #2ba470;
  transform: translateY(4px);
}
.btn-green.animate {
  text-indent: -9000px;
}
.btn-green.animate::after {
  position: absolute;
  content: "";
  width: 2.1875rem;
  height: 2.1875rem;
  border: 4px solid #ffffff;
  border-radius: 50%;
  border-left-color: transparent;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: spin ease-in 2.5s forwards;
  animation-name: spin;
  transition-timing-function: ease-in-out;
  animation-duration: 2.5s;
  animation-fill-mode: forwards;
}
.btn-green.animate.success::before {
  position: absolute;
  content: "";
  width: 2.1875rem;
  height: 1.09375rem;
  border: 6px solid #ffffff;
  border-radius: 10%;
  border-right: 0;
  border-top: 0;
  left: 50%;
  top: 50%;
  transform: scale(0);
  animation: success ease-in 0.15s forwards;
  animation-delay: 2.5s;
}

@keyframes spin {
  0% {
    transform: trnaslate(-50%, -50%) rotate(0deg) scale(1);
  }
  90% {
    transform: translate(-50%, -50%) rotate(1080deg) scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes success {
  from {
    transform: translate(-150%, -50%) rotate(0) scale(0);
  }
  to {
    transform: translate(-50%, -50%) rotate(-45deg) scale(1);
  }
}
.form-group {
  position: relative;
  margin-bottom: 1.5625rem;
}
.form-group .error-icon::after {
  content: url(assets/images/icon-error.svg);
  position: absolute;
  top: 1.125rem;
  right: 1.75rem;
}
.form-group .error {
  text-align: right;
  margin-top: 0.375rem;
  font-style: italic;
}

.hero {
  color: aqua;
  max-width: 31.9375rem;
}
.hero__text {
  margin-bottom: 1.875rem;
  line-height: 3.665625rem;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  gap: 4.375rem;
}